.contentblock{

    width:80vw;
    margin: 0 auto; 
}
.fieldset{
    color:blue;
    display:flex;
    flex-wrap: wrap;
    border-radius: 10px;
}
.flex{
    align-items: center;
    display:flex;
}
.profile-pic{
    object-fit: cover;
    border-radius: 50%;
}
.verticalFlex{
    display:flex;
    flex-direction: column;
}
.flexbox{
    background-image: url('/cham/IMG_9166.jpg');
    background-repeat: no-repeat; 
    background-size: cover;
    display:flex;
    flex-direction: column;
    gap:10px;
    margin:0; 
    padding:0;
}
/*Set the background color of dark-mode to black 
  and remove the background photo.*/
.dark-flexbox{
    display:flex;
    flex-direction: column;
    gap:10px;
    background-image: none;
    background-color: black;
    color: white;
    margin:0; 
    padding:0;
}
/*Set the background of some elements to grey in dark mode.*/
.dark-flexbox .about-content,
.dark-flexbox mat-toolbar,
.dark-flexbox .send,
.dark-flexbox .input{
    background-color: grey;
}

mat-toolbar {
    color:#fff;
    background-color:blueviolet;
}
.toolbar-row-flex {
    display: flex; 
    flex-wrap: wrap; 
    /* Make space between flex items */
    justify-content: space-between; 
    /* Align items vertically to the center */
    align-items: center; 
}

div{
    margin:0.5em;
}

swiper-container {
    width: 80%;
    display: block;
    margin: auto;
}
  
swiper-slide {
    display: flex;
    flex-direction: column;  
    align-items: center;    
    justify-content: center; 
    text-align: center;      
}
  
swiper-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;  
}

h3{
    color:blue;
}